module.exports = "<h2 id=\"overview\">\n  <span>Overview</span>\n  <a class=\"heading-anchor fa fa-link\" href=\"#overview\"></a>\n</h2><p>Prax is a framework of time and state for React applications. It’s heavily\ninspired by Clojure and ClojureScript libraries, particularly Reagent, and has\nbeen battle-tested in large apps since 2015. In terms of popular JS libraries,\nPrax’s role in your app can be roughly described as: Redux with addons +\nImmutableJS + RxJS + Rust-style runtime for deterministic object destructors.</p>\n<p>Another, simplistic, way to describe Prax: it’s like React for <em>everything</em>, not\njust for views.</p>\n<p>Prax is not buzz-driven, not particularly functional, object-oriented,\nevent-based, or FRP-centric. It builds on solid ideas carefully pilfered from\nmultiple languages and frameworks, it tries hard to be general and not\nover-abstract, and it attacks real, hard problems.</p>\n<p>Dive into the <a href=\"api\">API Reference</a> and <a href=\"examples\">examples</a>. Read forth for the\n<a href=\"#problems-and-solutions\">motivation</a> and <a href=\"#big-ideas\">big ideas</a> behind Prax.</p>\n<h2 id=\"problems-and-solutions\">\n  <span>Problems and Solutions</span>\n  <a class=\"heading-anchor fa fa-link\" href=\"#problems-and-solutions\"></a>\n</h2><p>Problem: reactive UI and event-driven logic requires subscriptions and\nunsubscriptions. This is equivalent to manual memory management, and we\nshouldn’t have to do it.</p>\n<ul>\n<li><p>Non-solution: manual subscriptions; humans can’t be trusted with cleanup.\nAnother non-solution: specialized “declarative” APIs like <code>react-redux</code>. They\ntend to be specialized for one purpose. We can do better.</p>\n</li>\n<li><p>Prax solution: reactivity driven by <em>procedural</em> data access. See\n<a href=\"api#-praxcomponent-\"><code>PraxComponent</code></a> for a UI example,\n<a href=\"examples#reactive-logic\">Reactive Logic</a> for a side effect example, and\n<a href=\"examples#reactive-computations\">Reactive Computations</a> for a data example.</p>\n</li>\n</ul>\n<p>Problem: unused resources should automatically deinitialize. I call this\n<a href=\"misc#_demand-driven_\">demand-driven design</a>. When nobody’s looking, you should\nevict data from cache, abort requests, close websockets, etc., and it should be\npart of your reactivity model. This is crucial for a subscription-based API such\nas Firebase.</p>\n<ul>\n<li><p>Non-solution: using only immutable data and pure functions. Many\n<a href=\"misc#_resource_\">resources</a> are inherently mutable, you can’t pretend that\nyou’re only dealing with data; pure functions can’t track subscribers.</p>\n</li>\n<li><p>Prax solution: lazy resources that init/deinit on demand; see\n<a href=\"examples#demand-driven-resources\">example</a>. Define multiple resources that\nmanage the lifecycle of immutable data.</p>\n</li>\n</ul>\n<p>Problem: coordinating async activities that depend on multiple conditions.\nExample: loading user profile when authorized, evicting it when deauthorized,\nand reloading it if the user has changed. It’s even harder to write asynchronous\ncode that may start an activity, then <em>restart</em> or <em>change</em> it when the\nconditions change.</p>\n<ul>\n<li><p>Non-solution: ignoring the problem. It leads to nasty race conditions such as\ntrying to load private data before authorising the user. Another non-solution:\nusing promises or any inherently asynchronous abstraction. It leads to UI jank.</p>\n</li>\n<li><p>Prax solution: expressing logic and side effects as reactive definitions. See\n<a href=\"examples#reactive-logic\">Reactive Logic</a>.</p>\n</li>\n</ul>\n<h2 id=\"big-ideas\">\n  <span>Big Ideas</span>\n  <a class=\"heading-anchor fa fa-link\" href=\"#big-ideas\"></a>\n</h2><p><a href=\"api#-praxcomponent-\">Implicit reactivity</a> driven by procedural data access.</p>\n<p><a href=\"examples#demand-driven-resources\">Demand-driven resources</a>, friendly to lazy\ninit/deinit and cache eviction.</p>\n<p><a href=\"examples#reactive-logic\">Reactive logic</a>, defining side effects as a function\nof state.</p>\n<p>Combine the strengths of FP and OOP. Store your data in immutable, functional\ndata types, manipulating it with <a href=\"api#emerge\">generic functions</a>. Conduct the\n<em>flow</em> of data and side effects with OO tools such as\n<a href=\"examples#demand-driven-resources\">lazy observables</a> and\n<a href=\"examples#event-system\">event streams</a>, or the novel tools offered by Prax.</p>\n<p>Centralize your resources and data into a hierarchy of ownership, starting with\na root object. Make the root available everywhere in the app. This avoids the\nneed for over-designed props, dependency injection, etc.</p>\n<h2 id=\"inspiration\">\n  <span>Inspiration</span>\n  <a class=\"heading-anchor fa fa-link\" href=\"#inspiration\"></a>\n</h2><ul>\n<li>Clojure, <code>clojure.core</code>: functional data structures, atoms</li>\n<li>Redux: centralization</li>\n<li>Reagent, Re-frame: implicit reactivity</li>\n<li>Redux, Re-frame: global event broadcast</li>\n<li>Erlang: pattern matching</li>\n<li>Haskell: higher order functions</li>\n<li>Rust: ownership and lifetimes</li>\n</ul>\n";

